@use 'variables' as v;
@use 'mixins' as m;

.container {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
    padding: 5rem;
}

.reset-list {
    list-style: none;
    padding: 0;
    margin: 0;
}

.none-display {
    display: none;
}

.button {
    background: none;
    border: none;
}

.display-none {
    display: none;
}
